<template>
  <div class="homePage">
    <div class="operate">
      <el-form :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="CX">
          <el-select v-model="searchForm.CX" placeholder="CX" clearable>
            <el-option
              v-for="(item, index) in CX_OPTION"
              :key="index"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="SFZCKMC">
          <el-select v-model="searchForm.SFZCKMC" placeholder="SFZCKMC" clearable>
            <el-option
              v-for="(item, index) in SFZCKMC_OPTION"
              :key="index"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSearch">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-table
      :data="tableData"
      stripe
      border
      height="calc(100% - 120px)"
      style="width: 100%"
    >
      <el-table-column
        prop="XH"
        label="XH">
      </el-table-column>
      <el-table-column
        prop="CKSJ"
        label="CKSJ">
      </el-table-column>
      <el-table-column
        prop="CX"
        label="CX">
      </el-table-column>
      <el-table-column
        prop="SFZRKMC"
        label="SFZRKMC">
      </el-table-column>
      <el-table-column
        prop="RKSJ"
        label="RKSJ">
      </el-table-column>
      <el-table-column
        prop="BZ"
        label="BZ">
      </el-table-column>
      <el-table-column
        prop="SFZCKMC"
        label="SFZCKMC">
      </el-table-column>
      <el-table-column
        prop="CP"
        label="CP">
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @current-change="handleCurrentChange"
        layout="total, prev, pager, next"
        :page-size="pageSize"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  name: "homePage",
  data() {
    return {
      CX_OPTION: [],
      SFZCKMC_OPTION: [],
      searchForm: {
        CX: "",
        SFZCKMC: "",
      },
      rawData: [],  //原始数据
      rawDataFilter: [],  //筛选后的数据
      tableData: [], //当前分页数据
      total: 0,
      pageSize: 100, //每页数
    }
  },
  created() {
    // const api = "http://124.71.207.74:5000/test_api";
    const api = "/test_api";
    this.axios.get(api).then((response) => {
      //原始数据
      this.rawData = response.data;
      this.rawDataFilter = response.data;
      this.total = response.data.length;
      //默认选择第一行
      this.handleCurrentChange(1)
      //下拉数据处理
      this.CX_OPTION = new Set(response.data.map(item => item.CX));
      this.SFZCKMC_OPTION = new Set(response.data.map(item => item.SFZCKMC));
    })
  },
  methods: {
    handleCurrentChange(val) {
      const startIndex = (val - 1) * this.pageSize;
      const endIndex = val  * this.pageSize;
      this.tableData = this.rawDataFilter.slice(
        startIndex, endIndex
      )
    },
    onSearch() {
      this.rawDataFilter = this.rawData;
      const { CX, SFZCKMC } = this.searchForm;
      this.rawDataFilter = this.rawDataFilter.filter(item => {
        if (CX) {
          if(SFZCKMC) {
            return item.CX === CX && item.SFZCKMC === SFZCKMC;
          }else {
            return item.CX === CX;
          }
        } else {
          if(SFZCKMC) {
            return item.SFZCKMC === SFZCKMC;
          }else {
            return true;
          }
        }
      });
      this.total = this.rawDataFilter.length;
      //默认选择第一行
      this.handleCurrentChange(1)
    }
  }
}
</script>
<style scoped lang="scss">
div.homePage {
  height: 100%;
  .operate {
    height: 40px;
    line-height: 40px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .el-form {
      height: 100%;
    }
  }
  .el-table {
    margin-bottom: 20px;
  }
  div.pagination {
    height: 36px;
    line-height: 36px;
    .el-pagination {
      float: right;
      box-sizing: border-box;
    }
  }
}

</style>
